import { Injectable, InjectionToken, Inject } from '@angular/core';
import { Logger, ILoggerBase } from 'everiscore';

export const LoggerConfig = new InjectionToken<ILoggerBase>('loggerBase');

@Injectable()
export class EvLogger extends Logger {
  constructor(@Inject(LoggerConfig) _config: ILoggerBase) {
    super(_config);
  }
}
